/* src/app/complex/complex-results/complex-navigator/complex-list-display-buttons/complex-list-display-buttons.component.css */
.displayButton {
  text-align: center;
}
.view-button {
  width: 225px;
  margin-right: 5px;
  background-color: white;
  color: #555;
  border: 1px dotted var(--primary);
}
.view-button:hover {
  color: var(--primary);
  background-color: rgb(234, 234, 234);
  border: transparent;
  border-bottom: 1px dotted var(--primary);
}
.selected,
.selected:hover {
  background-color: var(--primary);
  color: white;
}
.new-tag {
  color: red;
  font-weight: bold;
  font-size: x-small;
}
.new-tag-selected {
  color: yellow;
}
.cnButton {
  outline-offset: 0;
  outline: rgba(0, 124, 130, 0.75) solid 1px;
  animation: animateOutline 4s ease infinite;
}
.cnButton.selected {
  animation: none;
}
@keyframes animateOutline {
  0%, 50%, 100% {
    outline-width: 6px;
    outline-offset: 4px;
    outline-color: rgba(0, 124, 130, 0);
  }
  10% {
    outline-color: rgba(0, 124, 130, 0.75);
  }
}
